    }

    [class^='wpte-product-border-reveal']:before,
    [class*=' wpte-product-border-reveal']:before {
        top: 5px;
    }

    [class^='wpte-product-border-reveal']:after,
    [class*=' wpte-product-border-reveal']:after {
